export default {
  props: {
    post: {
      type: Object,
      required: false,
    },
  },

  data() {
    return {
      editedPost: this.post
        ? { ...this.post }
        : {
            author: '',
            title: '',
            thumbnail: '',
            content: '',
            preview: '',
          },
    }
  },
  methods: {
    submit() {
      // save the post
      this.$emit('submit', this.editedPost)
    },
    onCancel() {
      // Navigate back to Admin
      this.$router.push('/admin/')
    },
  },
}
