import axios from 'axios'
import moment from 'moment'
import AdminPostForm from '@/components/AdminPostForm'
export default {
  components: {
    AdminPostForm,
  },
  methods: {
    createPost(postData) {
      this.$store.dispatch('addPost', postData).then(() => {
        this.$router.push('/admin/')
      })
    },
  },
}
